.up-button_active {
  background-color: transparent;
  position: fixed;
  bottom: 30px;
  right: 30px;
  border: 5px orange solid;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  font-size: 20px;
  box-sizing: border-box;
  text-align: center;
  text-decoration: none;
  color: orange;
  opacity: 1;
  display: block;
  transition: 0.4s linear;
}
